// Helper function to search airports with improved matching
export const searchAirports = (query: string): Airport[] => {
  const searchTerm = query.toLowerCase().trim();
  
  if (searchTerm.length === 0) {
    return airports.slice(0, 20); // Return first 20 airports for empty search
  }

  // Create a scoring system for better results
  const scoredResults = airports.map(airport => {
    let score = 0;
    const searchLower = searchTerm.toLowerCase();
    
    // Exact IATA code match (highest priority)
    if (airport.iata_code.toLowerCase() === searchLower) {
      score += 1000;
    }
    // IATA code contains search term
    else if (airport.iata_code.toLowerCase().includes(searchLower)) {
      score += 500;
    }
    
    // City name exact match
    if (airport.city.toLowerCase() === searchLower) {
      score += 800;
    }
    // City name contains search term
    else if (airport.city.toLowerCase().includes(searchLower)) {
      score += 400;
    }
    
    // Airport name contains search term
    if (airport.name.toLowerCase().includes(searchLower)) {
      score += 300;
    }
    
    // Country name contains search term
    if (airport.country.toLowerCase().includes(searchLower)) {
      score += 200;
    }
    
    // Check aliases
    const aliases = airportAliases[searchLower] || [];
    if (aliases.includes(airport.iata_code.toLowerCase())) {
      score += 600;
    }
    
    // Partial word matches in city name
    const cityWords = airport.city.toLowerCase().split(/\s+/);
    const searchWords = searchLower.split(/\s+/);
    const matchingWords = searchWords.filter(word => 
      cityWords.some(cityWord => cityWord.includes(word))
    );
    if (matchingWords.length > 0) {
      score += matchingWords.length * 100;
    }
    
    return { airport, score };
  });

  // Filter out non-matches and sort by score
  return scoredResults
    .filter(result => result.score > 0)
    .sort((a, b) => b.score - a.score)
    .map(result => result.airport)
    .slice(0, 50); // Limit results to 50
};

// Helper function to get airport by IATA code
export const getAirportByIATA = (iataCode: string): Airport | undefined => {
  return airports.find(airport => airport.iata_code === iataCode.toUpperCase());
};

// Helper function to get popular airports (major hubs)
export const getPopularAirports = (): Airport[] => {
  const popularIATAs = [
    'JFK', 'LAX', 'LHR', 'CDG', 'FRA', 'AMS', 'SIN', 'HKG', 'NRT', 'ICN',
    'PEK', 'PVG', 'DXB', 'DOH', 'SYD', 'MEL', 'GRU', 'YYZ', 'YVR', 'MEX',
    'YUL', 'MIA', 'ORD', 'DFW', 'ATL', 'SFO', 'DEN', 'SEA', 'BOS', 'EWR'
  ];
  
  return airports.filter(airport => popularIATAs.includes(airport.iata_code));
};

// Helper function to get airports by country
export const getAirportsByCountry = (country: string): Airport[] => {
  return airports.filter(airport => 
    airport.country.toLowerCase() === country.toLowerCase()
  );
};

// Helper function to get airports by city
export const getAirportsByCity = (city: string): Airport[] => {
  return airports.filter(airport => 
    airport.city.toLowerCase() === city.toLowerCase()
  );
}; 